.contact-sec-top {
  background-repeat: no-repeat;
  background: url("https://www.learnglobal.com//asset/start/images/banner.jpg");
  background-size: cover;
  background-position: top center;
  padding-bottom: 60px;
}
.contact-form {
  width: 100%;
  margin: 35px auto 0;
  background: #ffffff;
}
.contact-img {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  margin-right: -15px;
  margin-left: -15px;
}
section.banner-heading.contact {
  margin-top: 88px;
  background: #f1f1f1;
}

.contact-img {
  background-image: url("https://www.learnglobal.com//asset/start/images/contact__1.jpg");
  width: 100%;
  background-size: cover;
  background-repeat: no-repeat;
  margin: 35px auto;
  padding: 0;
  background-position: center;
}

.contact-form input.form-control {
  background: transparent;
  border-radius: 0px !important;
  border-bottom: 1px solid #bababa;
  padding: 0px;
  border-right: none;
  border-left: none;
  border-top: none;
}
.select .form-control {
  background: transparent;
  border-radius: 0px !important;
  border-bottom: 1px solid #bababa;
  padding: 0px;
  border-top: none;
  border-left: none;
  border-right: none;
}
.area {
  background: transparent;
  border-radius: 0px !important;
  padding: 0px;
  width: 50%;
  padding: 10px;
}
.contact-form .form-group.fieldd {
  width: 46%;
  float: left;
}
.contact-form .form-group.last-name {
  width: 46%;
  float: right;
}
.contact-form .form-group {
  margin-bottom: 2rem;
}
.contact-form-buttton button.btn.btn-primary {
  background: #e22;
  border: none;
  padding: 10px 23px;
  margin-top: 15px;
}
.contact-form-buttton button.btn.btn-primary:hover {
  background: #333;
}
.contact-sec-top {
  height: 400px;
  background: steelblue;
}
.contact-sec-top h2 {
  text-align: center;
  margin: auto;
}
section.contact {
  padding-bottom: 50px;
}
.innercont {
  text-align: center;
  display: table;
  background: rgba(238, 34, 35, 0.94);
  padding: 30px 20px;
  margin: 306px auto 0;
  color: #fff;
}
.contact-form form input,
.contact-form form select {
  font-size: 13px;
}
.contact_banner {
  background: url("https://www.learnglobal.com//asset/start/images/contactbanner.jpg");
  padding: 217px 0 120px;
  background-position: center;
  position: relative;
  text-align: center;
}
.innercont p {
  margin-bottom: 0;
}
.contact_banner h3 {
  color: #fff;
  font-size: 62px;
  font-weight: 100;
  display: table;
  margin: auto;
  position: relative;
}

.contact_banner:before {
  position: absolute;
  background: rgba(0, 0, 0, 0.54);
  content: "";
  height: 100%;
  width: 100%;
  display: block;
  left: 0;
  top: 0;
}
