.error {
  color: red;
  float: left;
}
.error {
  color: red;
}
.agentpart .sfgdu.justify-content-center.align-self-center {
  padding-top: 100px;
  width: 1170px;
}
.agentpart .login-box {
  background: #f3f3f3;
  margin-top: 30px;
  border: 1px solid #cccc;
  margin-bottom: 30px;
}
h4.agentHeading {
  text-align: left;
  font-size: 18px;
  border-bottom: 2px solid #ff6067;
  padding-bottom: 14px;
  margin-bottom: 24px;
  color: #ed1a24;
}
.step__2,
.step__3 {
  margin-top: 20px;
}
.checkboxmore .checkbox {
  margin: 30px 40px 10px 0;
  display: inline-block;
  float: left;
}
.peryear {
  clear: both;
  display: block;
  text-align: left;
}
.peryear input,
.marketingMethod input {
  margin-left: 20px;
}
.peryear input:first-child,
.marketingMethod input {
  margin: 0;
}
.marketingMethod {
  clear: both;
  text-align: left;
}
.marketingMethod .checkbox {
  display: inline-block;
  margin-right: 18px;
}

.form-group.avgserviceFEE input:first-child,
.form-group.refer_students input:first-child {
  margin: 0;
}
.form-group.avgserviceFEE,
.form-group.refer_students {
  clear: left;
  text-align: left;
}
.form-group.avgserviceFEE input,
.form-group.refer_students input {
  margin-left: 30px;
}
.form-group.signupBTN button.sbmitbtnLogin {
  background: #212529;
  border: none;
  color: #fff;
  padding: 10px 30px;
  max-width: 150px;
  border-radius: 50px;
  width: 100%;
  float: left;
  margin-top: 10px;
  cursor: pointer;
}
p.decided {
  text-align: left;
  color: #676767;
  font-size: 12px;
}
p.decided input[type="checkbox"] {
  position: relative;
  top: 2px;
}
.agentpart .error {
  font-size: 14px;
}
.agentpart .form-group {
  text-align: left;
}
.agentpart .form-control.error {
  border: 1px solid red;
}
.checkboxmore label.error {
  float: left;
  clear: left;
  position: absolute;
}
